// src/services/purchaseService.ts

import { Purchase, Detail } from "../types";

const API_BASE_URL = "/api/8081/delivery-service/purchase";

type CreatePurchaseRequest = {
  purchase: Omit<Purchase, "id">;
  details: Omit<Detail, "id" | "id_purchase">[];
};

const authHeader = (): HeadersInit => {
    const token = localStorage.getItem("accessToken");
    return token ? { Authorization: `Bearer ${token}` } : {};
};

/**
 * GET /purchase
 * Recupera todas las compras (requiere JWT).
 */
export async function getPurchases(): Promise<{ purchases: Purchase[] }> {
  const res = await fetch(API_BASE_URL, {
    headers: authHeader(),
  });
  if (res.status === 401) {
    // opcional: intentar refresh automático
    // const tokens = await refresh({ refreshToken: localStorage.getItem("refreshToken")! });
    // return getPurchases();
    throw new Error("No autorizado");
  }
  if (!res.ok) throw new Error("Error al obtener compras");
  return res.json();
}

/**
 * GET /purchase/:id
 * Recupera una compra por su ID (requiere JWT).
 */
export async function getPurchaseById(id: number): Promise<Purchase> {
  const res = await fetch(`${API_BASE_URL}/${id}`, {
    headers: authHeader(),
  });
  if (res.status === 401) throw new Error("No autorizado");
  if (!res.ok) throw new Error(`Compra con ID ${id} no encontrada`);
  const json = await res.json();
  return json.purchase as Purchase;
}

/**
 * GET /purchase/user/:id
 * Recupera todas las compras de un usuario (requiere JWT).
 */
export async function getPurchasesByUserId(
  userId: number
): Promise<{ purchases: Purchase[] }> {
  const res = await fetch(`${API_BASE_URL}/user/${userId}`, {
    headers: authHeader(),
  });
  if (res.status === 401) throw new Error("No autorizado");
  if (!res.ok) throw new Error("Error al obtener compras de usuario");
  return res.json();
}

/**
 * POST /purchase
 * Crea una nueva compra con sus detalles.
 *
 * @returns El ID de la compra recién creada.
 */
export async function createPurchase(
  data: CreatePurchaseRequest
): Promise<number> {
  const res = await fetch(API_BASE_URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      ...authHeader(),
    },
    body: JSON.stringify(data),
  });
  if (res.status === 401) throw new Error("No autorizado");
  if (!res.ok) {
    const err = await res.json().catch(() => null);
    throw new Error(err?.error || "Error al crear la compra");
  }
  const json = await res.json();
  return json.id as number;
}
