// app/(admin)/visualizar/page.tsx
"use client";

import { useState, useEffect, ChangeEvent } from "react";
import { Trash2, Funnel, LoaderCircle } from "lucide-react";

import {
  getProducts,
  updateProduct,
  deleteProduct,
} from "@/lib/services/productService";
import { getUsers } from "@/lib/services/userService";
import { getPurchases } from "@/lib/services/purchaseService";

import type { Producto, User, Purchase } from "@/lib/types";

type View = "productos" | "usuarios" | "pedidos";

const TAG_OPTIONS: string[] = [
  "bebida","frío","dulce","panadería","refresco","snack",
  "krusty","salado","chicle","revista","lectura","caliente","comida"
];

export default function VisualizarPage() {
  const [view, setView] = useState<View>("productos");
  const [dropdownOpen, setDropdown] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string|null>(null);

  const [searchId, setSearchId] = useState("");
  const [orderId, setOrderId] = useState("");
  const [userId, setUserId] = useState("");

  const [productos, setProductos] = useState<Producto[]>([]);
  const [users, setUsers] = useState<User[]>([]);
  const [orders, setOrders] = useState<Purchase[]>([]);

  const [editingId, setEditingId] = useState<number|null>(null);
  const [editData, setEditData] = useState<Partial<Producto>>({});

  // 1) Carga inicial
  useEffect(() => {
    const fetchAll = async () => {
      setLoading(true);
      try {
        const pr = await getProducts();
        setProductos(pr.products);
        const ur = await getUsers();
        setUsers(ur.users);
        const or = await getPurchases();
        setOrders(or.purchases);
      } catch (e) {
        console.error(e);
        setError("No se pudieron cargar los datos");
      } finally {
        setLoading(false);
      }
    };
    fetchAll();
  }, []);

  // 2) Filtrados
  const filteredProducts = searchId
    ? productos.filter(p => p.id.toString() === searchId)
    : productos;
  const filteredUsers = searchId
    ? users.filter(u => u.id.toString() === searchId)
    : users;
  const filteredOrders = orders
    .filter(o => orderId ? o.id.toString() === orderId : true)
    .filter(o => userId  ? o.id_user.toString() === userId : true);

  // 3) Edit inline
  const startEdit = (p: Producto) => {
    setEditingId(p.id);
    setEditData({
      ...p,
      tags: [...(p.tags||[])],
      options: { ...(p.options||{}) },
    });
  };
  const cancelEdit = () => {
    setEditingId(null);
    setEditData({});
  };
  const saveEdit = async () => {
    if (editingId == null) return;
    try {
      // Llamada API para actualizar
      await updateProduct(editingId, editData);
      // Re‑fetch inmediato del listado
      const pr = await getProducts();
      setProductos(pr.products);
    } catch (e) {
      console.error(e);
      alert("No se pudo guardar el producto");
    } finally {
      cancelEdit();
    }
  };

  // 4) Eliminar
  const deleteProducto = async (id: number) => {
    if (!confirm("¿Eliminar producto?")) return;
    try {
      await deleteProduct(id);
      setProductos(prev => prev.filter(p => p.id !== id));
    } catch (e) {
      console.error(e);
      alert("Error al eliminar producto");
    }
  };

  // 5) Manejadores de campo
  const onChangeField = (field: keyof Producto, val: string) =>
    setEditData(prev => ({ ...prev, [field]: field==="price"? +val : val }));
  const onChangeImage = (e: ChangeEvent<HTMLInputElement>) => {
    const f = e.target.files?.[0];
    if (f) setEditData(prev => ({ ...prev, image: f.name }));
  };
  const onChangeOption = (key:string,csv:string) =>
    setEditData(prev => ({
      ...prev,
      options: { ...(prev.options||{}), [key]: csv.split(",").map(v=>v.trim()) }
    }));
  const toggleTag = (tag:string) =>
    setEditData(prev => {
      const arr = prev.tags||[];
      return {
        ...prev,
        tags: arr.includes(tag)
          ? arr.filter(t=>t!==tag)
          : [...arr,tag]
      };
    });

  // 6) Loading / Error
  if (loading) {
    return <main className="min-h-screen flex items-center justify-center">
      <LoaderCircle className="w-12 h-12 animate-spin"/>
    </main>;
  }
  if (error) {
    return <main className="min-h-screen flex items-center justify-center">
      <p className="text-red-500">{error}</p>
    </main>;
  }

  // 7) Render
  return (
    <section className="relative bg-[#e9d29a] p-6 pb-32">
      <div className="relative mb-6 mt-8 w-full">
        <div className="relative w-[80%] rounded-lg border-2 border-blue-400 bg-white px-8 py-4 shadow-md">
          <p className="text-lg font-semibold text-gray-800">
            ¡Por la gracia de los Squishees! Inspecciona usuarios, pedidos y productos…
          </p>
        </div>
      </div>

      <FiltersBar
        view={view}
        dropdownOpen={dropdownOpen}
        setDropdown={setDropdown}
        setView={setView}
        searchId={searchId}
        setSearchId={setSearchId}
        orderId={orderId}
        setOrderId={setOrderId}
        userId={userId}
        setUserId={setUserId}
      />

      {view==="productos" && (
        <ProductsTable
          productos={filteredProducts}
          tagOptions={TAG_OPTIONS}
          editingId={editingId}
          editData={editData}
          startEdit={startEdit}
          cancelEdit={cancelEdit}
          saveEdit={saveEdit}
          deleteProducto={deleteProducto}
          onChangeField={onChangeField}
          onChangeImage={onChangeImage}
          onChangeOption={onChangeOption}
          toggleTag={toggleTag}
        />
      )}
      {view==="usuarios" && <UsersTable users={filteredUsers} />}
      {view==="pedidos"  && <OrdersTable orders={filteredOrders} />}
    </section>
  );
}

// ——————————————————————————————————————————————————
// FiltersBar + FilterInput
// ——————————————————————————————————————————————————
interface FiltersBarProps {
  view:View; dropdownOpen:boolean;
  setDropdown:(b:boolean)=>void;
  setView:(v:View)=>void;
  searchId:string; setSearchId:(s:string)=>void;
  orderId:string;  setOrderId:(s:string)=>void;
  userId:string;   setUserId:(s:string)=>void;
}
function FiltersBar({
  view, dropdownOpen,
  setDropdown, setView,
  searchId,setSearchId,
  orderId,setOrderId,
  userId,setUserId
}:FiltersBarProps){
  return (
    <div className="mb-6 flex items-center justify-center space-x-6">
      <span className="font-semibold">Filtrar por:</span>
      <div className="relative inline-block">
        <button
          onClick={()=>setDropdown(!dropdownOpen)}
          className="flex items-center gap-2 rounded-full border-2 border-green-300 bg-white px-5 py-3 shadow hover:bg-gray-100"
        >
          <Funnel size={16}/> Abrir filtros
        </button>
        {dropdownOpen && (
          <ul className="absolute z-20 mt-2 w-40 rounded border bg-white shadow-lg">
            {(["productos","usuarios","pedidos"] as View[]).map(v=>(
              <li key={v}>
                <button
                  onClick={()=>{
                    setView(v);
                    setDropdown(false);
                    setSearchId("");
                    setOrderId("");
                    setUserId("");
                  }}
                  className={`block w-full px-5 py-3 text-left hover:bg-green-200 ${
                    view===v?"bg-green-300 font-semibold":""
                  }`}
                >
                  {v[0].toUpperCase()+v.slice(1)}
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>
      {view==="pedidos"?
        <>
          <FilterInput label="ID pedido" val={orderId}   onChange={setOrderId}/>
          <FilterInput label="ID usuario" val={userId}  onChange={setUserId}/>
        </>
        :
        <FilterInput label="ID" val={searchId} onChange={setSearchId}/>
      }
    </div>
  );
}
interface FilterInputProps {
  label:string; val:string;
  onChange:(v:string)=>void;
}
const FilterInput = ({label,val,onChange}:FilterInputProps)=>(  
  <div className="flex items-center gap-2">
    <label className="font-semibold">{label}:</label>
    <input
      type="text"
      value={val}
      onChange={e=>onChange(e.target.value)}
      placeholder="..."
      className="rounded-full border-2 border-green-300 bg-white px-5 py-3 shadow-sm focus:ring-2 focus:ring-green-400"
    />
  </div>
);

// ——————————————————————————————————————————————————
// ProductsTable
// ——————————————————————————————————————————————————
interface ProductsTableProps{
  productos:Producto[];
  tagOptions:string[];
  editingId:number|null;
  editData:Partial<Producto>;
  startEdit:(p:Producto)=>void;
  cancelEdit:()=>void;
  saveEdit:()=>void;
  deleteProducto:(id:number)=>void;
  onChangeField:(f:keyof Producto,v:string)=>void;
  onChangeImage:(e:ChangeEvent<HTMLInputElement>)=>void;
  onChangeOption:(k:string,v:string)=>void;
  toggleTag:(t:string)=>void;
}
function ProductsTable({
  productos, tagOptions,
  editingId, editData,
  startEdit, cancelEdit, saveEdit,
  deleteProducto,
  onChangeField, onChangeImage,
  onChangeOption, toggleTag
}:ProductsTableProps){
  return (
    <div className="overflow-auto">
      <h2 className="mb-2 text-xl font-semibold">Productos</h2>
      <table className="min-w-full rounded bg-white shadow">
        <thead className="bg-gray-100">
          <tr>
            {["ID","Imagen","Nombre","Precio","Descripción","Etiquetas","Opciones","Acciones"].map(h=>(
              <th key={h} className="px-4 py-2">{h}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {productos.map(p=>{
            const editing = editingId===p.id;
            return (
              <tr key={p.id} className="border-t">
                <td className="px-4 py-2">{p.id}</td>
                <td className="px-4 py-2">
                  {editing?
                    <input type="file" accept="image/*" onChange={onChangeImage}/>
                  :
                    <img src={p.image} alt={p.name} className="h-12 w-12 rounded object-contain"/>
                  }
                </td>
                <td className="px-4 py-2">
                  {editing?
                    <textarea
                      rows={2}
                      className="w-full rounded border px-2 py-1"
                      value={editData.name||""}
                      onChange={e=>onChangeField("name",e.target.value)}
                    />
                  :
                    p.name
                  }
                </td>
                <td className="px-4 py-2 text-right">
                  {editing?
                    <input
                      type="number"
                      className="w-full rounded border px-2 py-1 text-right"
                      value={editData.price!=null?editData.price:""}
                      onChange={e=>onChangeField("price",e.target.value)}
                    />
                  :
                    `$${(p.price||0).toFixed(2)}`
                  }
                </td>
                <td className="px-4 py-2">
                  {editing?
                    <textarea
                      rows={3}
                      className="w-full rounded border px-2 py-1"
                      value={editData.description||""}
                      onChange={e=>onChangeField("description",e.target.value)}
                    />
                  :
                    p.description
                  }
                </td>
                <td className="px-4 py-2">
                  {editing?
                    <div className="flex flex-wrap gap-1">
                      {tagOptions.map(tag=>{
                        const sel = editData.tags?.includes(tag);
                        return (
                          <span
                            key={tag}
                            onClick={()=>toggleTag(tag)}
                            className={`cursor-pointer rounded-full border px-2 py-1 ${
                              sel?"border-green-500 bg-green-300":"border-gray-300 bg-white"
                            }`}
                          >
                            {tag}
                          </span>
                        );
                      })}
                    </div>
                  :
                    (p.tags||[]).join(", ")
                  }
                </td>
                <td className="px-4 py-2">
                  {editing?
                    <div className="space-y-2">
                      <small className="text-gray-600">Separa por comas</small>
                      {Object.entries(editData.options||{})
                        .filter(([,v])=>Array.isArray(v))
                        .map(([k,v])=>(
                          <div key={k} className="flex flex-col">
                            <label className="font-semibold capitalize">{k}:</label>
                            <input
                              className="rounded border px-2 py-1"
                              value={(v as string[]).join(", ")}
                              onChange={e=>onChangeOption(k,e.target.value)}
                            />
                          </div>
                        ))}
                    </div>
                  :
                    Object.entries(p.options||{})
                      .filter(([,v])=>Array.isArray(v))
                      .map(([k,v])=>`${k}: ${(v as string[]).join(", ")}`)
                      .join(" | ")
                  }
                </td>
                <td className="px-4 py-2 text-center">
                  {editing?
                    <div className="mt-4 flex flex-col items-center space-y-2">
                      <div className="flex space-x-2">
                        <button
                          onClick={saveEdit}
                          className="rounded bg-green-500 px-3 py-1 text-white hover:bg-green-600"
                        >
                          Guardar
                        </button>
                        <button
                          onClick={()=>deleteProducto(p.id)}
                          className="rounded bg-red-500 p-2 text-white hover:bg-red-600"
                        >
                          <Trash2 size={16}/>
                        </button>
                      </div>
                      <button
                        onClick={cancelEdit}
                        className="rounded bg-gray-300 px-3 py-1 text-gray-800 hover:bg-gray-400"
                      >
                        Cancelar
                      </button>
                    </div>
                  :
                    <div className="flex items-center justify-center space-x-2">
                      <button
                        onClick={()=>startEdit(p)}
                        className="rounded bg-blue-500 px-3 py-1 text-white hover:bg-blue-600"
                      >
                        Editar
                      </button>
                      <button
                        onClick={()=>deleteProducto(p.id)}
                        className="rounded bg-red-500 p-2 text-white hover:bg-red-600"
                      >
                        <Trash2 size={16}/>
                      </button>
                    </div>
                  }
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

// ——————————————————————————————————————————————————
// UsersTable
// ——————————————————————————————————————————————————
interface UsersTableProps { users:User[] }
const UsersTable = ({users}:UsersTableProps)=>(  
  <div>
    <h2 className="mb-2 text-xl font-semibold">Usuarios</h2>
    <table className="min-w-full rounded bg-white shadow">
      <thead className="bg-gray-100">
        <tr>
          {["ID","Nombre","Email"].map(h=>(
            <th key={h} className="px-4 py-2 text-left">{h}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {users.map(u=>(
          <tr key={u.id} className="border-t">
            <td className="px-4 py-2">{u.id}</td>
            <td className="px-4 py-2">{u.username}</td>
            <td className="px-4 py-2">{u.email}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);

// ——————————————————————————————————————————————————
// OrdersTable
// ——————————————————————————————————————————————————
interface OrdersTableProps { orders:Purchase[] }
const OrdersTable = ({orders}:OrdersTableProps)=>(  
  <div>
    <h2 className="mb-2 text-xl font-semibold">Pedidos</h2>
    <table className="min-w-full rounded bg-white shadow">
      <thead className="bg-gray-100">
        <tr>
          {["ID pedido","ID usuario","Estado","Total"].map(h=>(
            <th
              key={h}
              className={`px-4 py-2 ${
                h==="Total"?"text-right":"text-left"
              }`}
            >
              {h}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {orders.map(o=>(
          <tr key={o.id} className="border-t">
            <td className="px-4 py-2">{o.id}</td>
            <td className="px-4 py-2">{o.id_user}</td>
            <td className="px-4 py-2">{o.status}</td>
            <td className="px-4 py-2 text-right">
              ${(o.total||0).toFixed(2)}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);
